.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #00000083;
  border-radius: 12px;
  background-color: #66edff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: hsl(0, 0%, 89%);
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  color: whitesmoke;
  font-weight: none;
  font-size: 0.6rem;
  text-align: center;
}
